/* src/App.css */
body {
margin: 0;
font-family: 'Segoe UI', sans-serif;
background-color: #141414; /* negro tipo Netflix */
color: #fff;
}
.cecyflix-logo {
font-size: 88px;
font-weight: bold;
color: #e50914; /* rojo Netflix */
text-align: center;
letter-spacing: 5px;
font-family: 'Bebas Neue', Gadget, sans-serif;
margin-bottom: 20px;
margin-top: -70px;
}
.App {
display: flex;
height: 100vh;
align-items: center;
justify-content: center;
}

form {
background-color: #1f1f1f;
padding: 40px;
border-radius: 10px;
width: 300px;
box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.1);
}
input {
width: 100%;
padding: 12px;
margin: 10px 0;
border: none;
border-radius: 4px;
font-size: 16px;
}
button {
width: 100%;
padding: 12px;
margin-top: 10px;
background-color: #e50914; /* rojo Netflix */
color: white;
font-weight: bold;
border: none;
border-radius: 4px;
cursor: pointer;
}
button:hover {
background-color: #f40612;
}
h2 {
text-align: center;
margin-bottom: 20px;
}
p {
text-align: center;
color: #aaa;
margin-top: 10px;
}

.gallery {
display: grid;
grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
gap: 20px;
padding: 20px;
background-color: #121212;
margin-top: 300px;
}
.card {
background-color: #1f1f1f;
padding: 20px;
border-radius: 12px;
color: #fff;
transition: transform 0.3s ease, box-shadow 0.3s ease;
box-shadow: 0px 2px 10px rgba(255, 255, 255, 0.1);
}
.card:hover {
transform: scale(1.05);
box-shadow: 0 0 20px #e50914;
}
h3 {
color: #e50914;
}
@media (max-width: 600px) {
.gallery {
grid-template-columns: 1fr;
 }
}
